@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
*{padding:0;margin:0}html,body{width:100%;height:100%}img{max-width:100%}body{background-color:#434343;font-family:"Open Sans",sans-serif;color:#fff}h1,h2,h3,h4,h5,h6{font-family:"Montserrat",sans-serif;text-transform:uppercase;margin:2px 0}h1{font-size:4rem}h2{font-size:3rem}h3{font-size:2rem}h4{font-size:2rem}h5{font-size:1.5rem}h6{font-size:1rem}ul{list-style:none}.fa,.fas{font-size:1rem}


nav{width:100%;height:100px;display:flex;align-items:center}nav ul{display:flex}nav ul>li{margin:0 75px}nav ul>li a{color:rgba(255,255,255,0.4);text-decoration:none;font-style:normal;font-weight:normal;font-size:18px;line-height:18px;transition:0.3s}nav ul>li a:hover{color:white;text-shadow:0 0 20px #ABFFA9}

header{display:flex;justify-content:space-evenly;align-items:center;height:calc(100vh - 100px);padding:0 130px}header h1{margin-bottom:40px;font-family:"Montserrat",sans-serif;font-style:normal;font-weight:bold;font-size:64px;line-height:64px}header p{font-family:"Montserrat",sans-serif;font-style:normal;font-weight:normal;font-size:24px;line-height:24px;margin-bottom:40px}header a{text-decoration:none;color:#fff;text-align:center;background:#3F3F3F;border-radius:13px;padding:20px 120px;box-sizing:border-box;border:1px solid #434343;transition:0.3s}header a:hover{border:1px solid #A9FAFF}section#team{height:100vh;display:flex;justify-content:space-evenly;align-items:center;padding:30px 130px}section#team h1{margin-bottom:40px;font-family:"Montserrat",sans-serif;font-style:normal;font-weight:bold;font-size:64px;line-height:64px}section#team p{font-family:"Montserrat",sans-serif;font-style:normal;font-weight:normal;font-size:24px;line-height:24px;margin-bottom:40px;width:70%}section#team img{height:700px}.line-text{position:absolute;-webkit-transform:rotate(-90deg);transform:rotate(-90deg);bottom:150px;left:0;color:rgba(255,255,255,0.4)}

section#distribution{display:flex;justify-content:space-evenly;align-items:center;padding:30px 130px}section#distribution .chart-info{display:flex;flex-direction:column;align-items:center;width:100%}section#distribution .chart-info .chart{width:90%;height:400px}section#distribution .chart-info .chart p{margin:15px 0}section#distribution .chart-info .chart-placeholder{width:900px;height:400px;background-color:#3F3F3F;display:flex;justify-content:center;align-items:center}section#distribution .info{width:70%}section#distribution .info h1{margin-bottom:40px;font-family:"Montserrat",sans-serif;font-style:normal;font-weight:bold;font-size:50px;line-height:64px}section#distribution .info p{font-family:"Montserrat",sans-serif;font-style:normal;font-weight:normal;font-size:20px;line-height:24px;margin-bottom:40px;width:70%}.radio{display:flex;margin:20px 10px}.radio>label{margin:0 10px;font-family:"Open Sans",sans-serif}.btn{width:150px;height:35px;border-radius:10px;cursor:pointer;outline:none;border:none;transition:0.3s;color:#434343;margin:0 20px 20px 20px}.submit{background-color:rgba(171,255,169,0.5)}.submit:hover{background-color:#abffa9;color:#3F3F3F}.chart-action{width:100%;padding:0 30px;box-sizing:border-box}.reset{background-color:rgba(247,87,87,0.5);color:rgba(255,255,255,0.5)}.reset:hover{background-color:#f75757;color:white}.select-section{display:flex}.select-section>select{background-color:#434343;padding:0 10px;margin:0 20px;color:white;width:150px;height:35px;border:1px solid white;box-sizing:border-box;border-radius:10px}

